import Vue from 'vue'
import App from './App.vue'
import Buefy from 'buefy'
import 'buefy/dist/buefy.css'
import Vuelidate from 'vuelidate'
import './scss/main.scss'
import { library } from '@fortawesome/fontawesome-svg-core'
import mainMixin from './mixins/main-mixin'

import {
  faPlus, faEdit, faPencilAlt, faGrinBeamSweat, faArrowUp, faArrowDown, faAngleRight, faTrash, faUndo, faSave, faExclamationCircle
} from '@fortawesome/free-solid-svg-icons'

import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

library.add(faPlus, faEdit, faPencilAlt, faGrinBeamSweat, faArrowUp, faArrowDown, faAngleRight, faTrash, faUndo, faSave, faExclamationCircle)

Vue.component('vue-fontawesome', FontAwesomeIcon)
Vue.config.productionTip = false
Vue.use(Vuelidate)
Vue.use(Buefy, {
  defaultIconComponent: 'vue-fontawesome',
  defaultIconPack: 'fas',
})
Vue.mixin(mainMixin)

new Vue({
  render: h => h(App),
}).$mount('#app')
