<template>
  <div class="add-task">
    <div class="field is-horizontal">
      <div class="field-body">
        <div class="field" style="max-width: 20rem">
          <b-input
            v-model="newTask.title"
            size="is-medium"
            placeholder="Task Title"
            :expanded="false"
            :loading="isLoading"
            icon="edit"
          />
        </div>
        <div class="field">
          <b-input
            v-model="newTask.description"
            placeholder="Task Description..."
            size="is-medium"
            :expanded="true"
            :loading="isLoading"
            icon="pencil-alt"
          />
        </div>
        <b-button
          size="is-medium" type="is-primary" icon-left="plus"
          :disabled="$v.newTask.$invalid"
          :loading="isLoading"
          @click="createNewTask"
        />
      </div>
    </div>
    <transition>
      <div
        class="message is-danger"
        v-if="errorData"
      >
        <div class="message-body">
          <span class="tag is-medium is-danger has-text-weight-bold">
            {{ errorData.status }}
          </span>
          <span style="padding-left: 1rem">{{ errorData.message }}</span>
        </div>
      </div>
    </transition>
  </div>
</template>
<script>
import { required, minLength, maxLength } from 'vuelidate/lib/validators'
import { SnackbarProgrammatic as Snackbar } from 'buefy'
import moment from 'moment'

export default {
  name: 'AddTask',
  data () {
    return {
      isLoading: false,
      newTask: {
        title: '',
        description: '',
        date: null
      },
      errorData: null
    }
  },
  methods: {
    async createNewTask () {
      this.$v.newTask.$touch()
      this.errorData = null
      if (this.$v.newTask.$invalid === false) {
        this.$emit('loading', true)
        this.newTask.date = moment().toISOString()
        const response = await this.postRequest('tasks', this.newTask)
        if (response.status !== 201) {
          if (response.error) {
            this.errorData = {
              status: response.status,
              message: response.error.getMessage ? response.error.getMessage() : response.error
            }
          } else {
            this.errorData = { status: response.status, message: 'El código debe ser 201' }
          }
          Snackbar.open({
            message: 'Error procesando la petición',
            type: 'is-danger',
          })
        } else {
          this.newTask.title = ''
          this.newTask.description = ''
          this.$emit('update')
          Snackbar.open({
            message: 'Tarea creada correctamente',
            type: 'is-success',
          })
        }
        this.$emit('loading', false)
      }
    }
  },
  validations: {
    newTask: {
      title: {
        required,
        minLength: minLength(3),
        maxLength: maxLength(50)
      },
      description: {
        required,
        minLength: minLength(3),
        maxLength: maxLength(500)
      }
    }
  }
}
</script>
