<template>
  <div class="box">
    <b-table
      :data="tasks ? tasks : []"
      :striped="true"
      :hoverable="true"
      :loading="loading"
      :opened-detailed="defaultOpenedDetails"
      :show-detail-icon="true"
      detailed
      detail-key="description"
      checkable
      checkbox-position="right"
      :header-checkable="false"
      :checked-rows="checkedTasks"
      @check="onTaskChecked"
      sortable
    >
      <template slot-scope="props">
        <b-table-column field="task_id" label="ID" width="40" numeric>
          {{ props.row.task_id }}
        </b-table-column>
        <b-table-column field="title" label="Task Title">
          <b-input
            v-if="editTask === props.row"
            v-model="props.row.title"
          />
          <span
            v-else
            :class="{'has-line-through has-text-grey-light is-italic': props.row.completed}"
          >{{ props.row.title }}</span>
        </b-table-column>
        <b-table-column field="date" label="Date" numeric>
          {{ moment(props.row.date).format('HH:mm - D MMMM YYYY') }}
        </b-table-column>
        <b-table-column field="unix" label="UNIX" sortable :visible="false">
          {{ props.row.unix }}
        </b-table-column>
      </template>
      <template slot="detail" slot-scope="props">
        <article class="media">
          <figure class="media-left">
            <p class="image is-64x64">
              <b-icon icon="edit" custom-class="has-text-grey-lighter" />
            </p>
          </figure>
          <div class="media-content">
            <div class="content">
              <b-input
                v-if="editTask === props.row"
                v-model="editTaskDescription"
                type="textarea"
                maxlength="500"
              />
              <p v-else class="has-text-grey is-size-5">
                {{ props.row.description }}
              </p>
              <div class="buttons is-right">
                <template v-if="editTask != props.row">
                  <b-button type="is-info" icon-left="pencil-alt" class="is-uppercase" @click="editTask = props.row">
                    Editar
                  </b-button>
                  <b-button type="is-danger" icon-left="trash" class="is-uppercase" @click="confirmDelete(props.row)">
                    Eliminar
                  </b-button>
                </template>
                <template v-else>
                  <b-button type="is-warning" icon-left="undo" class="is-uppercase" @click="reloadTasks">
                    Cancelar
                  </b-button>
                  <b-button type="is-success" icon-left="save" class="is-uppercase"
                            @click="updateTask(props.row)">
                    Guardar
                  </b-button>
                </template>
              </div>
            </div>
          </div>
        </article>
      </template>
      <template slot="empty">
        <section class="section">
          <div class="content has-text-grey has-text-centered">
            <p>
              <b-icon
                pack="fas"
                icon="grin-beam-sweat"
                custom-size="5x"
                size="is-large"
              />
            </p>
            <p class="is-size-3">No tenemos tareas!</p>
          </div>
        </section>
      </template>
    </b-table>
  </div>
</template>
<script>
import { SnackbarProgrammatic as Snackbar } from 'buefy'
import moment from 'moment'

export default {
  name: 'TaskList',
  props: {
    loading: { type: Boolean, default: false }
  },
  data () {
    return {
      tasks: null,
      defaultOpenedDetails: [],
      checkedTasks: [],
      moment: moment,
      editTask: null,
      editTaskDescription: null
    }
  },
  watch: {
    editTask (task) {
      this.editTaskDescription = task ? task.description : null
    }
  },
  created () {
    this.reloadTasks()
    this.moment.locale('es')
  },
  methods: {
    async reloadTasks () {
      this.$emit('loading', true)
      const response = await this.getRequest('tasks')
      this.editTask = null
      if (response.status === 200) {
        const newTasks = response.data.map(t => {
          t.unix = moment(t.date).unix()
          return t
        })
        newTasks.sort((a, b) => {
          if (a.completed !== b.completed) {
            return b.completed ? -1 : 1
          } else {
            return b.unix - a.unix
          }
        })
        this.tasks = newTasks
        this.checkedTasks = this.tasks.filter(t => t.completed)
      } else if (response.status === 204) {
        this.tasks = []
      } else {
        Snackbar.open({
          message: 'Error cargando lista de tareas',
          type: 'is-danger',
        })
      }
      this.$emit('loading', false)
    },
    async onTaskChecked (_, checkedTask) {
      this.$emit('loading', true)
      /* eslint no-console: "off" */
      checkedTask.completed = !checkedTask.completed
      await this.updateTask(checkedTask)
      this.$emit('loading', false)
    },
    confirmDelete (task) {
      this.$buefy.dialog.confirm({
        title: 'Confirmación:',
        message: '¿Seguro que quieres <b>eliminar</b> esta Tarea?',
        confirmText: 'Elimnar Tarea',
        type: 'is-danger',
        hasIcon: true,
        onConfirm: () => this.deleteTask(task)
      })
    },
    async deleteTask (task) {
      const response = await this.deleteRequest(`tasks/${task.task_id}`)
      if ([200, 204].includes(response.status)) {
        Snackbar.open({
          message: 'Tarea Eliminada Correctamente',
          type: 'is-success',
        })
        await this.reloadTasks()
      } else {
        Snackbar.open({
          message: 'Error Eliminando Tarea',
          type: 'is-danger',
        })
      }
    },
    async updateTask (task) {
      if (this.editTask && this.editTaskDescription) {
        task.description = this.editTaskDescription
      }
      const response = await this.putRequest(`tasks/${task.task_id}`, task)
      if ([200, 204].includes(response.status)) {
        Snackbar.open({
          message: 'Tarea Actualizada',
          type: 'is-success',
        })
        await this.reloadTasks()
      } else {
        Snackbar.open({
          message: 'Error Actualizando Tarea',
          type: 'is-danger',
        })
      }
    }
  }
}
</script>
