import axios from 'axios'

export default {
  methods: {
    getRequest (relUrl, extraHeaders) {
      return this.performRequest(axios.get, ['http://localhost:8080/api/' + relUrl, null, { extraHeaders }])
    },
    postRequest (relUrl, data, extraHeaders) {
      return this.performRequest(axios.post, ['http://localhost:8080/api/' + relUrl, data, { extraHeaders }])
    },
    putRequest (relUrl, data, extraHeaders) {
      return this.performRequest(axios.put, ['http://localhost:8080/api/' + relUrl, data, { extraHeaders }])
    },
    deleteRequest (relUrl, extraHeaders) {
      return this.performRequest(axios.delete, ['http://localhost:8080/api/' + relUrl, null, { extraHeaders }])
    },
    performRequest (axiosFnx, fnxParams) {
      return new Promise(
        (resolve) => {
          axiosFnx.apply(null, fnxParams).then(resolve).catch(
            err => {
              /* eslint no-console: "off" */
              console.log('ERROR CATCH', err)
              if (err && err.response) {
                const response = err.response
                response.error = err
                resolve(response)
              } else {
                resolve({ status: 0, error: 'Network ERROR' })
              }
            }
          )
        }
      )
    }
  }
}
